#mySidenev {
    font-family:'Roboto', sans-serif;
}
.sidenav {
    height: 100%; 
    width: 0; 
    position: fixed; 
    z-index: 1;
    top: 0; 
    left: 0;
    background-color: #111; 
    overflow-x: hidden; 
    padding-top: 20px; 
    transition: 0.5s;
  }
  
  
  .sidenav a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #fffbfb;
    display: block;
    transition: 0.3s;
  }
  
  
  